import React from 'react'
import {connect} from 'react-redux'

const Input = (props)=>{
    console.log(props);
    return (
        <div>
            <input onKeyDown={(e)=>{if(e.keyCode==13){
                props.dispatch({type:'add_todo',value:e.target.value})
                }
            }} 
            type="text"
            />

        </div>
    )
}

export default connect()(Input)




